<template lang="html">
  <div class="address">
    <div class="address-list1 borderB">
      <p class="address-list__title">{{title}}</p>
      <p class="address-list__detail2">{{detail}}</p>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    address: {
      type: String,
      default: ''
    },
    title: {
      type: String,
      default: ''
    },
    detail: {
      type: String,
      default: ''
    }
  },
  methods: {
    del () {
      this.$emit('del')
    }
  }
}
</script>

<style lang="less" scoped>
@import '../../assets/css/variable.less';

.address {
  background-color: @whiteColor;
  .address-list1 {
    font-size: .3rem;
    padding: .1rem 0 .1rem 0;
    p {
      line-height: .5rem;
      color: @defaultColor;
    }
    .address-list__detail2 {
      color: @lightColor;
    }
  }
}
</style>



