<template lang="html">
  <div class="search-input">
    <div class="search-input-item">
      <input ref="searchInput" type="text" :autofocus="autofocus" @input="keyUpSearch" :style="{'background': inputBg}" :placeholder="placeholder">
      <span :style="{'color': btnColor}" @click="clickHandle">{{btnText}}</span>
      <i class="search__icon"></i>
    </div>
  </div>
</template>

<script>
import _ from 'underscore'
export default {
  props: {
    placeholder: {
      type: String,
      default: '请输入'
    },
    inputBg: {
      type: String
    },
    autofocus: {
      type: Boolean,
      default: false
    },
    btnText: {
      type: String,
      default: '搜索'
    },
    btnColor: {
      type: String,
      default: '#2cc482'
    }
  },
  data () {
    return {
      value: ''
    }
  },
  methods: {
    keyUpSearch: _.throttle(function (e) {
      this.value = e.target.value
      this.$emit('keyup-search', e.target.value)
    }, 800),
    clickHandle () {
      if (this.btnText === '搜索') {
        // this.$refs.searchInput.value = ''
        this.$emit('cancel-handle', this.value)
      }
      if (this.btnText === '确定') {
        this.$emit('ok-handle')
      }
    }
  }
}
</script>

<style lang="less" scoped>
@import '../../assets/css/variable.less';
@import '../../assets/css/property.less';

.search-input {
  .search-input-item {
    height: .88rem;
    line-height: .88rem;
    position: relative;
    padding: 0 1.06rem 0 .2rem;
    input {
      height: .68rem;
      width: 100%;
      padding-left: .52rem;
      font-size: .3rem;
      color: @lightColor;
      border: none;
      border-radius: .05rem;
      &::placeholder {
        color: @lightColor;
      }
    }
    span {
      position: absolute;
      right: .22rem;
      top: 0;
      font-size: .32rem;
    }
    .search__icon {
      left: .22rem;
      top: .2rem;
    }
  }
}
</style>



