<template lang="html">
  <div class="search-address-page">
    <!-- 头部 -->
    <default-head header="搜索地点"></default-head>

    <form action="#">
      <search-input @keyup-search="keyUpSearch" @cancel-handle="search" :autofocus="true"></search-input>
      <!-- 地址列表 -->
      <div class="address-data" v-for="(item, index) in addArr">
        <address-list2 :title="item.title" :detail="item.address" :index="item.index" @click.native="saveLocation(item)"></address-list2>
      </div>
      <div class="" id="allmap" style="display:none"></div>
    </form>

  </div>
</template>

<script>
import utils from '@/utils/utils'
import { mapGetters } from 'vuex'
import minutils from '@/utils/minUtils'
import defaultHead from '@/components/head/defaultHead'
import searchInput from '@/components/common/searchInput'
import addressList from '@/components/common/addressList'
import addressList2 from '@/components/common/addressList2'

export default {
  data () {
    return {
      map: {},
      value: '',
      delToggle: '删除',
      addArr: []
    }
  },
  computed: {
    ...mapGetters({location: 'getLocation'})
  },
  methods: {
    changeStates () {
      this.isShow = !this.isShow
      this.isShow ? this.delToggle = '删除' : this.delToggle = '取消'
    },
    delAddress (index) {
      this.addressDate.splice(index, 1)
    },
    keyUpSearch (val) {
      this.value = val
    },
    search () {
      if (!this.value) return
      minutils.IndicatorOpen('加载中...')

      this.map = new BMap.Map('allmap')
      var point = new BMap.Point(this.location.lng, this.location.lat)
      this.map.centerAndZoom(point, 18)
      setTimeout(function () {
        minutils.IndicatorClose()
      }, 3000)
      utils.searchComplete(this.map, this.value, this.addArr = [], function () {
        minutils.IndicatorClose()
      })
    },
    saveLocation (item) {
      let data = {
        city: item.city,
        district: item.district,
        lat: item.point.lat,
        lng: item.point.lng,
        province: item.province,
        street: item.title,
        streetNumber: ''
      }
      this.$store.dispatch('setLocation', data)
      this.$router.push('/search')
    }
  },
  components: { defaultHead, addressList, searchInput, addressList2 }
}
</script>

<style lang="less">
@import '../assets/css/variable.less';

.search-address-page {
  padding-top: .88rem;
  .head-right {
    color: @activeColor;
  }
  .address-data {
    padding: 0 .22rem;
    background: @whiteColor;
  }
}
</style>



